input[type=text] {
    background-color: rgba(255, 255, 255, 0);
    width: 100%;
    padding: 12px 20px;
    margin: 5px 0px;
    box-sizing: border-box;
    border: 3px solid rgb(233, 233, 233);
    border-radius: 15px;
    transition: 0.3s;
    outline: none;
  }

  input[type=text]:focus {
    border: 3px solid rgb(245, 116, 116);
  }

  input::-webkit-input-placeholder {
    color: rgb(66, 66, 66);
    font-size: 14px;
  }

  input:-ms-input-placeholder {
    color: rgb(66, 66, 66);
    font-size: 14px;
  }

  input::-ms-input-placeholder {
    color: rgb(66, 66, 66);
    font-size: 14px;
  }

  input::placeholder {
    color: rgb(66, 66, 66);
    font-size: 14px;
  }
  label, input {
    font-family: roboto;
    font-size: 14px;

  }

